<template>
	<div class="body">
		<div class="bg">

			<!-- <el-button plain style="margin-left: -2px;margin-top: -2px;position: fixed;z-index: 88888888;" >返回</el-button> -->

			<!-- <div class="title">
				<span><b>一等奖(2/2)</b> 奖品：HUAWEI Mate30Pro</span>
			</div> -->

			<div class="hy_txt animated zoomIn" v-show="is_txt">
				{{hy_txt}}
			</div>

			<div class="Listofwinners animated zoomIn" v-show="show2">
				<div class="Listofwinners_txt">
					<h2>恭喜获奖名单</h2>
					<p v-for="(item,index) in list" :key='index'>{{item.name}}</p>
				</div>
			</div>

			<div class="left_box">
				<!-- <el-select v-model="currentAward" placeholder="请选择" size='small'>
					<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled">
					</el-option>
				</el-select> -->
				<div class="cj"  @click="toggle">
					{{ks_stop}}
				</div>
				<!-- <el-button size="small" @click='$router.push({name:"Activity"})'>
					返回上一页
				</el-button> -->
			</div>

			<div id="myCanvasContainer" style="position: fixed;top:-20px;" class="animated zoomIn">
				<canvas :width="screenWidth" :height="screenHeight" id="myCanvas" ref="canvas">
					<p>换个现代浏览器吧！</p>
				</canvas>
			</div>

			<div id="tags" style="display: none;">
				<ul>
					<li v-for="(tag, index) in members" :key="index">
						<a href="#" style="height: auto;font-size: 40px">
							<div>
								{{tag.prize_name}}
							</div>
						</a>
					</li>
				</ul>
			</div>

		</div>
	</div>
</template>


<script>
	export default {
		data() {
			return {

				running: false,
				members: [], //数据

				hy_txt: '', //获奖弹窗文案
				is_txt: false, //获奖弹窗

				show2: false, //中奖名单
				list: [],

				ks_stop: '开始抽奖',

				screenWidth: document.documentElement.clientWidth, //屏幕宽度
				screenHeight: document.documentElement.clientHeight, //屏幕高度

			}
		},

		watch: {
			currentAward: function() {
				this.batchPlayers = [];
			},
		},

		methods: {
			toggle: function() {
				// console.log(this.running)
				if (this.running) {
					this.stop();
					this.ks_stop = '开始抽奖'

					this.$axios.post(this.$url + '/choujiang/draw')
						.then((res) => {
							// console.log(res.data.pojo.prize_name)
							if (res.data.result) {
								this.is_txt = true
								this.hy_txt = res.data.pojo.prize_name
								this.list.push({
									name: res.data.pojo.prize_name
								})
								setTimeout(() => {
									this.show2 = true
								}, 1000)
							} else {
								this.$message({
									message: res.data.msg,
									showClose: true,
									type: 'error'
								});
							}

						})
						.catch((res) => {
							console.log(res)
						})


				} else {
					this.start();
					this.ks_stop = '暂停'
					this.is_txt = false
				}
			},

			getSpeed: function() {
				return [0.1 * Math.random() + 0.01, -(0.1 * Math.random() + 0.01)];
			},

			start: function() {
				this.running = true;
				TagCanvas.SetSpeed('myCanvas', [5, 1]);
			},
			stop: function() {
				this.running = false;
				TagCanvas.SetSpeed('myCanvas', [0, 0]);
				setTimeout(() => {
					TagCanvas.SetSpeed('myCanvas', this.getSpeed());
				}, 500)
			},

			init: function() {
				try {
					TagCanvas.Start('myCanvas', 'tags', {
						textColour: "#ca2424",
						dragControl: 3,
						decel: 0.95,
						textHeight: 18,
						minSpeed: 0.01,
						shadow: '#333',
						shadowOffset: [0, 0],
						zoom: '0.85',
						wheelZoom: false,
						textFont: "微软雅黑",
						initial: [
							0.1 * Math.random() + 0.01,
							-(0.1 * Math.random() + 0.01),
						],
					});
				} catch (e) {
					// something went wrong, hide the canvas container
					document.getElementById('myCanvasContainer').style.display =
						'none';
				}
			},
			copy: function(obj) {
				return JSON.parse(JSON.stringify(obj));
			},
		},

		created: function() {
			console.log(2)
			// localStorage.removeItem('members')
			
			// this.awards = JSON.parse(localStorage.getItem('awards')) || [];
			// // if (this.awards.length === 0) {
			// //   location.href = 'setting.html';
			// // }

			// var result = JSON.parse(localStorage.getItem('result')) || [];
			// for (var i = 0; i < this.awards.length; i++) {
			// 	result[i] = result[i] || [];
			// }
			// this.result = result;

			// this.choosed = JSON.parse(localStorage.getItem('choosed')) || {};

			// var awards = JSON.parse(localStorage.getItem('awards')) || [];
			// this.rewards = awards.map(function(item, index) {
			// 	return {
			// 		level: index + 1,
			// 		prize_name: item.prize_name,
			// 		count: item.count,
			// 		editing: {},
			// 	};
			// });
		},

		mounted: function() {
			this.$axios.post(this.$url + '/choujiang/search', {})
				.then((res) => {
					// console.log(res.data.pojo)
					let members2 = res.data.pojo
					localStorage.setItem('members', JSON.stringify(members2))
					localStorage.setItem('players', JSON.stringify(members2))
					var members = JSON.parse(localStorage.getItem('members')) || window.members;
					console.log(members);
					this.players = JSON.parse(localStorage.getItem('players')) || members;
					this.members = this.copy(members);
					localStorage.setItem('players', JSON.stringify(this.players));
					var canvas = this.$refs.canvas;
					canvas.width = document.body.offsetWidth;
					canvas.height = document.body.offsetHeight;
					this.$nextTick(function() {
						this.init();
					});
					document.body.addEventListener('keyup', this.onKeyup)
					var _this = this;
					window.onresize = function() { // 定义窗口大小变更通知事件
						_this.screenWidth = document.documentElement.clientWidth; //窗口宽度
						_this.screenHeight = document.documentElement.clientHeight; //窗口高度
					};
				})
				.catch((res) => {
					console.log(res)
				})
			
		},
	};
</script>


<style scoped>
	.left_box {
		width: 80%;
		height: 35px;
		text-align: center;
		padding: 15px 0;
		position: fixed;
		bottom: 40px;
		left: 0;
		right: 0;
		z-index: 99;
		margin: auto;
		border-radius: 0 10px 10px 0;
	}

	.title {
		font-size: 35px;
		color: #fff;
		line-height: 1.5;
		padding: 20px;
		text-align: center;
		/* position: absolute; */
		width: 80%;
		/* background: darkcyan; */
		margin: 0 auto !important;
		margin-bottom: 20px;
	}

	.title b {
		color: yellow;
	}

	.hy_txt {
		display: inline-block;
		text-align: center;
		font-size: 20px;
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
		height: fit-content;
		background: #fff;
		line-height: initial;
		color: #d90007;
		font-weight: bold;
		border-radius: 8px;
		box-shadow: 1px 2px 3px rgb(223 49 49 / 35%);
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;
		z-index: 9999999;
		margin: auto;
		padding: 15px 30px;
	}

	.fade-enter-active,
	.fade-leave-active {
		transition: transform .5s;
		transform: scale(1);
	}

	.fade-enter,
	.fade-leave-to {
		opacity: 0;
		transform: scale(0.1);
	}


	.Listofwinners::-webkit-scrollbar {
		width: 4px;
	}

	.Listofwinners::-webkit-scrollbar-track {
		background-color: #fff;
		display: none;
		border-radius: 20px;
		overflow: hidden;
	}

	.Listofwinners::-webkit-scrollbar-thumb {
		background-color: #fff;
		border-radius: 20px;
		display: none;
		border-radius: 50px;
	}

	.Listofwinners::-webkit-scrollbar-thumb:hover {
		background-color: #fff;
		border-radius: 20px;
	}

	.Listofwinners::-webkit-scrollbar-thumb:active {
		background-color: #fff;
		border-radius: 20px;
	}

	.Listofwinners {
		width: 336px;
		height: 475px;
		background: white;
		position: fixed;
		top: 50px;
		right: 30px;
		margin: auto;
		padding: 10px;
		border-radius: 10px;
		overflow: hidden;
		overflow-y: scroll;
		z-index: 999999999;
		box-shadow: 0px 5px 5px rgb(223 49 49 / 18%);
	}

	.Listofwinners_txt {
		margin-bottom: 0px;
	}

	.Listofwinners_txt h2 {
		line-height: 60px;
		font-weight: bold;
		font-size: 26px;
		color: #b80004;
		text-align: center;
	}

	.Listofwinners_txt p {
		width: 100%;
		margin: 20px 0;
		font-size: 20px;
		text-align: center;
		color: #d90007;
		font-weight: bold;
	}

	.cj {
		width: 100px;
		background: #fff;
		color: #ce2524;
		padding: 10px 20px;
		text-align: center;
		margin: 0 auto;
		    font-size: 14px;
		    border-radius: 4px;
		box-shadow: 1px 2px 3px rgb(223 49 49 / 35%);
	}
	.cj:hover{
		width: 100px;
		background: #fff;
		color: #ce2524;
		box-shadow: 1px 2px 3px rgb(223 49 49 / 35%);
	}
</style>



